<template>
  <div class="hs-container">
    <c-header isBackNav>
      <div slot="middle" class="c-header-title">
        <h2 v-if="hotelOrder.City">
          {{$t('base.HotelName', 'function', hotelOrder.City[`CityName_${language}`])}}
          ({{`${Number(hotelOrder.TravelType) === 0 ? $t('base.Business') : $t('base.Personal')}`}})
        </h2>
        <p>{{$t(beTime(new Date(hotelOrder.CheckInDate)), 'date', ['MM月dd日', 'MM dd'])}}-{{$t(beTime(new Date(hotelOrder.CheckOutDate)), 'date', ['MM月dd日', 'MM dd'])}}&nbsp;{{getApartDayCount(hotelOrder.CheckInDate, hotelOrder.CheckOutDate)}}{{$t('base.QuantifierNight')}}</p>
      </div>
      <p slot="right" class="standard" v-if="Number(hotelOrder.TravelType) === 0" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>
    </c-header>
    <div class="search-bar">
      <div>
        <div class="date" @click="showDatePicker = true">
          <div :type="language">
            <p>{{$t('base.CI_2')}}</p>
            <p>{{$t(beTime(new Date(hotelOrder.CheckInDate)), 'date', ['MM-dd', 'MM dd'])}}</p>
          </div>
          <div :type="language">
            <p>{{$t('base.CO_2')}}</p>
            <p>{{$t(beTime(new Date(hotelOrder.CheckOutDate)), 'date', ['MM-dd', 'MM dd'])}}</p>
          </div>
        </div>
        <div class="search-key">
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')"></div>
          <input maxlength='50' type="text" v-model="QueryText" :placeholder="$t('base.KeywordSearch')" @keyup.enter="_getDatalist"/>
        </div>
        <p @click="_getDatalist">{{$t('base.Search_2')}}</p>
      </div>
    </div>
    <hs-scroll
      class="table-view"
      ref="tableView"
      :data="datalist"
      :options="tableViewOptions"
      @pulling-down="_getDatalist"
      @pulling-up="_getDatalistMore"
      :isListenScroll="true"
      @touchEnd="_touchEnd"
      @scrollStart="_scrollStart">
      <hs-null v-if="noValue(JSON.stringify(datalist))"></hs-null>
      <ul v-else>
        <hs-section>
          <infor-list-cell
            v-for="(item, index) in datalist" :key="index"
            :details="item"
            @click.native="$router.push({path: '/hotel/inforDetails', query: {HotelID: item.HotelId}})"></infor-list-cell>
        </hs-section>
        <div v-if="tableViewOptions.pullUpLoad === false" style="width: 100%;height: 50px;"></div>
      </ul>
    </hs-scroll>
    <div class="footer" ref="footer">
      <div class="item" @click="showSortDialog = true">
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/down.png')">
          <div class="dot" v-if="Sort !== 0"></div>
        </div>
        <p>{{$t('base.Sort')}}</p>
      </div>
      <div class="item" @click="showStarAndPriceDialog = true">
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/star.png')">
          <div class="dot" v-if="priceFilterItemsIndex !== 0 || StarRate !== ''"></div>
        </div>
        <p>{{$t('base.PriceRating')}}</p>
      </div>
      <div class="item" @click="showFilterHotel = true">
        <div class="logo" style="background-image: url('static/image/plane-ticket/book/screen.png')">
          <div class="dot" v-if="hasFilters"></div>
        </div>
        <p>{{$t('base.Filters')}}</p>
      </div>
    </div>
    <hs-dialog v-model="showSortDialog">
      <div class="sortDialog">
        <hs-cell-1 :showLogo="false" @click.native="[Sort = 0, _getDatalist()]">
          <div class="custom-cell" :class="{active: Sort === 0}">{{$t('base.Recommended')}}</div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" @click.native="[Sort = 2, _getDatalist()]">
          <div class="custom-cell" :class="{active: Sort === 2}">{{$t('base.PriceLowToHigh')}}</div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" @click.native="[Sort = 3, _getDatalist()]">
          <div class="custom-cell" :class="{active: Sort === 3}">{{$t('base.PriceHighToLow')}}</div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" @click.native="[Sort = 1, _getDatalist()]">
          <div class="custom-cell" :class="{active: Sort === 1}">{{$t('base.RatingHighToLow')}}</div>
        </hs-cell-1>
      </div>
    </hs-dialog>
    <hs-dialog v-model="showStarAndPriceDialog">
      <div class="starAndPriceDialog" @click.stop>
        <div>
          <div class="item-header">{{$t('base.RatingMultipleChoice')}}</div>
          <div class="items">
            <p
              v-for="(item, index) in starFilterItems" :key="index"
              @click="_selectStarFilterItem(index)"
              :class="{actived: StarRate.split(',').indexOf(`${index + 1}`) >= 0 || (index === 0 && StarRate === '')}"
              :style="{'margin-right': index % 3 === 2 ? '0px' : '', 'margin-bottom': index % 3 === 2 ? '20px' : ''}">{{item}}</p>
          </div>
        </div>
        <div>
          <div class="item-header">{{$t('base.PriceSingleChoice')}}</div>
          <div class="items">
            <p
              v-for="(item, index) in priceFilterItems" :key="index"
              @click="priceFilterItemsIndex = index"
              :style="{'margin-right': index % 3 === 2 ? '0px' : '', 'margin-bottom': index % 3 === 2 ? '20px' : ''}"
              :class="{actived: priceFilterItemsIndex === index}">{{item}}</p>
          </div>
        </div>
        <div class="button">
          <hs-button width="60%" height="40px" @click="[_getDatalist(), showStarAndPriceDialog = false]">{{$t('base.Search')}}</hs-button>
        </div>
      </div>
    </hs-dialog>
    <c-travel-standard v-model="showTravelStandard" :type="2" :details="hotelRight.HotelRank" :travelPolicy="hotelRight.TravelPolicy"></c-travel-standard>
    <hs-date-picker-2
      :show.sync="showDatePicker"
      :fromDate="hotelOrder.CheckInDate"
      @update:fromDate="_updateFromDate"
      :toDate="hotelOrder.CheckOutDate"
      @update:toDate="_updateToDate"></hs-date-picker-2>
    <hs-filter-hotel :show.sync="showFilterHotel" :details="filterDetails" @delegate="_delegateWithUpdateFilterDetails"></hs-filter-hotel>
  </div>
</template>
<script type="text/ecmascript-6">
// 酒店信息页面
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsButton from 'base/hs-button/hs-button'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsTab from 'base/hs-tab/hs-tab'
import InforListCell from './infor-list-cell'
import HsDialog from 'base/hs-dialog/hs-dialog'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import HsDatePicker2 from 'base/hs-date-picker/hs-date-picker-2'
import HsFilterHotel from 'base/hs-filter/hs-filter-hotel'
import HsNull from 'base/hs-null/hs-null'
import {kpiHotelGetHotelList, kpiHotelGetHotelFilterCondition} from 'api'
import {mapGetters, mapMutations, mapActions} from 'vuex'
export default {
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (vm.noValue(vm.hotelOrder.TravelType)) {
        vm.$router.replace('/home')
        return
      }
      if (from.path === '/hotel/query' || vm.noValue(JSON.stringify(vm.datalist)) || vm.noValue(JSON.stringify(vm.filterDetails))) {
        vm.priceFilterItemsIndex = 0
        vm.Sort = 0
        vm.StarRate = ''
        vm.QueryText = vm.$route.query.QueryText
        vm.$nextTick(() => {
          vm._delegateWithUpdateFilterDetails(vm.$route.params.filterDetails)
        })
        // vm._getFilterDetails()
      }
      if (vm.noValue(JSON.stringify(vm.hotelRight))) {
        vm.dispatchHotelRight()
      }
    })
  },
  beforeRouteLeave(to, from, next) {
    this.showSortDialog = false
    this.showStarAndPriceDialog = false
    this.showFilterHotel = false
    this.showTravelStandard = false
    this.showDatePicker = false
    next()
  },
  // activated() {
  //   if (this.noValue(this.hotelOrder.TravelType)) {
  //     this.$router.replace('/home')
  //   }
  // },
  computed: {
    ...mapGetters([
      'hotelOrder',
      'hotelRight'
    ]),
    hasFilters() {
      let res = false
      for (let key of Object.keys(this.filterDetails)) {
        this.filterDetails[key].forEach((element, index) => {
          this.filterDetails[key][index].selected === true && (res = true)
        })
      }
      return res
    }
  },
  watch: {
    priceFilterItemsIndex(val) {
      if (val === 0) {
        this.LowRate = undefined
        this.HighRate = undefined
      } else if (val === 1) {
        this.LowRate = 0
        this.HighRate = 150
      } else if (val === 2) {
        this.LowRate = 151
        this.HighRate = 300
      } else if (val === 3) {
        this.LowRate = 301
        this.HighRate = 450
      } else if (val === 4) {
        this.LowRate = 451
        this.HighRate = 600
      } else if (val === 5) {
        this.LowRate = 601
        this.HighRate = 1000
      } else if (val === 6) {
        this.LowRate = 1001
        this.HighRate = undefined
      }
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        },
        pullUpLoad: false
      },
      filterDetails: {},
      datalist: [],
      showSortDialog: false,
      showStarAndPriceDialog: false,
      showTravelStandard: false,
      showDatePicker: false,
      showFilterHotel: false,
      currentPageIndex: 1,
      currentPageSize: 30,
      starFilterItems: [this.$t('base.Any'), this.$t('base.StarEconomic'), this.$t('base.StarComfort'), this.$t('base.StarPremium'), this.$t('base.StarLuxury')],
      priceFilterItems: [this.$t('base.Any'), `${this.$t('base.Below_1')}￥150${this.$t('base.Below_2')}`, '￥151-￥300', '￥301-￥450', '￥451-￥600', '￥601-￥1000', `${this.$t('base.Over_1')}￥1000${this.$t('base.Over_2')}`],
      priceFilterItemsIndex: 0,
      Sort: 0, // 排序类型
      HighRate: undefined, // 最高价
      LowRate: undefined, // 最低价
      StarRate: '', // 星级
      QueryText: '', // 关键字
      BusinessZoomId: '', // 商圈
      DistrictId: '', // 行政区/地区
      Facilities: '', // 设施
      BrandId: '', // 品牌编码
      ThemeIds: '' // 酒店主题
    }
  },
  methods: {
    ...mapMutations([
      'SET_HOTEL_ORDER'
    ]),
    ...mapActions([
      'dispatchHotelRight'
    ]),
    _getDatalist() {
      this.datalist = []
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        CityID: this.hotelOrder.CityID,
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        HotelType: 0,
        Sort: this.Sort,
        StarRate: this.StarRate,
        LowRate: this.LowRate,
        HighRate: this.HighRate,
        QueryText: this.QueryText,
        BusinessZoomId: this.BusinessZoomId,
        DistrictId: this.DistrictId,
        Facilities: this.Facilities,
        BrandId: this.BrandId,
        ThemeIds: this.ThemeIds
      }
      kpiHotelGetHotelList(params).then((res) => {
        console.log('酒店列表: ', res)
        this.datalist = res.ResultData.Hotels
        if (res.ResultData.Hotels.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        CityID: this.hotelOrder.CityID,
        CheckInDate: this.beTimeNumber(this.hotelOrder.CheckInDate),
        CheckOutDate: this.beTimeNumber(this.hotelOrder.CheckOutDate),
        HotelType: 0,
        Sort: this.Sort,
        StarRate: this.StarRate,
        LowRate: this.LowRate,
        HighRate: this.HighRate,
        QueryText: this.QueryText,
        BusinessZoomId: this.BusinessZoomId,
        DistrictId: this.DistrictId,
        Facilities: this.Facilities,
        BrandId: this.BrandId,
        ThemeIds: this.ThemeIds
      }
      kpiHotelGetHotelList(params, true).then((res) => {
        console.log('酒店列表: ', res)
        this.datalist = this.datalist.concat(res.ResultData.Hotels)
        if (res.ResultData.Hotels.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _getFilterDetails() {
      let params = {
        cityID: this.hotelOrder.CityID
      }
      kpiHotelGetHotelFilterCondition(params).then((res) => {
        console.log('获取城市下的酒店筛选条件: ', res)
        if (res.ResultData.IsSuccess !== false) {
          this.filterDetails = res.ResultData
          for (let key of Object.keys(this.filterDetails)) {
            this.filterDetails[key].forEach((element, index) => {
              this.$set(this.filterDetails[key][index], 'selected', false)
            })
          }
        }
      })
    },
    // 选择入住日期回调
    _updateFromDate(val) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.CheckInDate = val
      this.SET_HOTEL_ORDER(res)
    },
    // 选择离店日期回调
    _updateToDate(val) {
      let res = JSON.parse(JSON.stringify(this.hotelOrder))
      res.CheckOutDate = val
      this.SET_HOTEL_ORDER(res)
      !this.noValue(val) && this._getDatalist()
    },
    // 选择筛选项-星级
    _selectStarFilterItem(index) {
      console.log(index)
      if (index === 0) {
        this.StarRate = ''
      } else {
        let list = this.noValue(this.StarRate) ? [] : this.StarRate.split(',')
        let indexInList = list.indexOf(`${index + 1}`)
        indexInList >= 0 ? list.splice(indexInList, 1) : list.push(`${index + 1}`)
        this.StarRate = list.join(',')
      }
    },
    _delegateWithUpdateFilterDetails(val = {}) {
      this.filterDetails = val
      console.log('筛选条件: ', val)
      this.BusinessZoomId = ''
      this.DistrictId = ''
      this.Facilities = ''
      this.BrandId = ''
      this.ThemeIds = ''
      if (val.CommericalInfoList) {
        let res = []
        val.CommericalInfoList.forEach(element => {
          element.selected && res.push(element.ID)
          this.BusinessZoomId = res.join(',')
        })
      }
      if (val.DistrictInfoList) {
        let res = []
        val.DistrictInfoList.forEach(element => {
          element.selected && res.push(element.ID)
          this.DistrictId = res.join(',')
        })
      }
      if (val.FacilitieInfoList) {
        let res = []
        val.FacilitieInfoList.forEach(element => {
          element.selected && res.push(element.ID)
          this.Facilities = res.join(',')
        })
      }
      if (val.BrandInfoList) {
        let res = []
        val.BrandInfoList.forEach(element => {
          element.selected && res.push(element.ID)
          this.BrandId = res.join(',')
        })
      }
      if (val.ThemeList) {
        let res = []
        val.ThemeList.forEach(element => {
          element.selected && res.push(element.ID)
          this.ThemeIds = res.join(',')
        })
      }
      this._getDatalist()
    },
    _scrollStart() {
      this.$refs.footer && (this.$refs.footer.style.bottom = '-50px')
    },
    _touchEnd(pos) {
      // console.log(pos.y)
      this.$refs.footer && (this.$refs.footer.style.bottom = '0px')
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsButton,
    HsSection,
    HsCell1,
    HsTab,
    InforListCell,
    HsDialog,
    CTravelStandard,
    HsDatePicker2,
    HsFilterHotel,
    HsNull
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .c-header-title
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 100%
    h2
      font-size 1.1rem
      font-weight bold
      line-height 1.2em
    p
      font-size 0.6rem
      color $color_gray_text
      line-height 1.5em
  .c-header-title[type="EN"]
    >h2
      font-size 0.8rem
    >p
      font-size 0.5rem
  .standard
    font-size 0.9rem
    color $color_black_text_0
  >.search-bar
    @extend .flex-center
    width 100%
    height 40px
    background-color $color_white
    >div
      @extend .flex-row
      @extend .flex-start
      width 310px
      height 32px
      border 1px solid $color_gray_layer
      border-radius 15px
      padding-left 15px
      >.date
        @extend .flex-column
        @extend .flex-center
        @extend .flex-top
        width 75px
        height 28px
        border-right 1px solid $color_gray_layer
        >div
          @extend .flex-row
          @extend .flex-start
          width 100%
          >p
            font-size 0.7rem
            line-height 1.1em
            &:nth-child(1)
              color $color_black_text_1
              margin-right 4px
            &:nth-child(2)
              color $color_theme
        >div[type="EN"]
          @extend .flex-between
          >p
            &:nth-child(2)
              margin-right 2px
      >.search-key
        @extend .flex-row
        @extend .flex-start
        width 170px
        height 30px
        padding-left 15px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          margin-right 5px
        >input
          width calc(100% - 25px)
          height 28px
          font-size 0.8rem
          color $color_black_text_1
      >p
        @extend .flex-center
        width 50px
        height 28px
        font-size 0.8rem
        color $color_black_text_1
        border-left 1px solid $color_gray_layer
  >.table-view
    width 100%
    height calc(100% - 45px - 40px)
    overflow hidden
  >.footer
    @extend .flex-row
    @extend .flex-between
    transition all 300ms
    position absolute
    bottom 0
    left 0
    width 100%
    height 50px
    background-color $color-black_1
    >.item
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      height 100%
      font-size 0.8rem
      color $color_white
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-bottom 5px
        position relative
        >.dot
          position absolute
          top -1px
          right 0
          background-color $color_theme
          width 6px
          height 6px
          border-radius 3px
  .starAndPriceDialog
    @extend .flex-column
    @extend .flex-end
    position absolute
    width 100%
    left 0
    bottom 0
    background-color $color_white
    padding 10px 20px
    >div
      width 100%
      margin-bottom 10px
      >.item-header
        @extend .flex-row
        @extend .flex-start
        font-size 1rem
        width 100%
        height 40px
        font-weight bold
      >.items
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        flex-wrap wrap
        width 100%
        >p
          width calc(33.33% - 13.33px)
          height 40px
          background-color $color_gray_layer
          color $color_black_text_0
          font-size 0.7rem
          line-height 40px
          text-align center
          margin-right 20px
          border-radius 2px
        >.actived
          background-color $color_white
          border 1px solid $color_theme
          color $color_theme
    >.button
      @extend .flex-center
      width 100%
      padding 20px 0
      margin-bottom 0px
  .sortDialog
    @extend .flex-column
    @extend .flex-end
    position absolute
    width 100%
    left 0
    bottom 0
    background-color $color_white
    .custom-cell
      @extend .flex-center
      width 100%
      height 40px
      font-size 0.8rem
      color $color_black_text_0
    .active
      color $color_theme
</style>



