<template>
  <div class="hotel-infor-list-cell">
    <div class="imageview">
      <img v-lazy="details.ThumbNailUrl"/>
    </div>
    <div class="details">
      <div class="infor">
        <p>{{details.HotelName}}</p>
        <div class="location">
          <div class="logo" style="background-image: url('static/image/hotel/location.png')"></div>
          <p>{{details.DistrictName}}{{details.Address}}</p>
        </div>
        <div class="logos" v-if="details.Facilities.indexOf(1) >= 0 || details.Facilities.indexOf(2) >= 0 || details.Facilities.indexOf(5) >= 0 || details.Facilities.indexOf(6) >= 0">
          <div class="logo" v-if="details.Facilities.indexOf(1) >= 0" style="background-image: url('static/image/hotel/service/FREE_WIFI.png')"></div>
          <div class="logo" v-if="details.Facilities.indexOf(2) >= 0" style="background-image: url('static/image/hotel/service/Charge_WIFI.png')"></div>
          <div class="logo" v-if="details.Facilities.indexOf(5) >= 0" style="background-image: url('static/image/hotel/service/FREE_PARK.png')"></div>
          <div class="logo" v-if="details.Facilities.indexOf(6) >= 0" style="background-image: url('static/image/hotel/service/Charge_PARK.png')"></div>
        </div>
        <div class="label">
          <p v-if="details.SourceLabels && details.SourceLabels.length > 0"
            v-for="(item, index) in details.SourceLabels" :key="index"
            :style="{'border-color': item.Color, color: item.Color}"
            class="agreement">{{item.Name}}</p>
        </div>
      </div>
      <div class="price">
        <p>￥{{Math.ceil(details.LowRate)}}</p>
        <p>{{$t('base.From')}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
export default {
  props: {
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  filters: {
    filterStarRateOptions(val) {
      switch (val) {
        case 0:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 1:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 2:
          return `${Vue.prototype.$t('base.Economic')}${Vue.prototype.$t('base.ClassHotel')}`
        case 3:
          return `${Vue.prototype.$t('base.StarComfort')}${Vue.prototype.$t('base.ClassHotel')}`
        case 4:
          return `${Vue.prototype.$t('base.StarPremium')}${Vue.prototype.$t('base.ClassHotel')}`
        case 5:
          return `${Vue.prototype.$t('base.StarLuxury')}${Vue.prototype.$t('base.ClassHotel')}`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.hotel-infor-list-cell
  @extend .flex-row
  @extend .flex-start
  width 100%
  // height 143px
  background-color $color_white
  border-bottom 3px solid $color_gray_background_0
  padding 10px
  position relative
  >.imageview
    width 90px
    overflow hidden
    position absolute
    top 10px
    left 10px
    bottom 10px
    min-height 90px
    border-radius 2px
    >img
      height 100%
      position absolute
      left 50%
      transform translateX(-50%)
  >.details
    @extend .flex-row
    @extend .flex-between
    width calc(100% - 88px)
    // height 100%
    min-height 90px
    margin-left 90px
    padding-left 6px
    >.infor
      width calc(100% - 74px)
      height 100%
      >p
        &:nth-child(1)
          font-size 1rem
          line-height 1.2em
          font-weight bold
          ellipsis(2)
          max-height 2.4em
      >.logos
        @extend .flex-row
        @extend .flex-start
        width 100%
        padding-left 20px
        margin-top 5px
        >.logo
          width 15px
          height 15px
          background-size 100% 100%
          margin-right 10px
      >.label
        @extend .flex-row
        @extend .flex-start
        margin-left 20px
        margin-top 5px
        >.category
        >.agreement
          font-size 0.7rem
          margin-right 5px
          padding 2px
          border 1px solid $color_green_text
          color $color_green_text
          border-radius 2px
        >.agreement
          border 1px solid $color_blue_text
          color $color_blue_text
      >.location
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        width 100%
        margin-top 10px
        >.logo
          width 16px
          height 16px
          background-size 100% 100%
          margin-right 4px
        >p
          font-size 0.7rem
          line-height 1.4em
          width calc(100% - 16px)
          color $color_black_text_1
          ellipsis(2)
          max-height 2.8em
    >.price
      @extend .flex-row
      @extend .flex-end
      width 70px
      >p
        &:nth-child(1)
          font-size 1rem
          font-weight bold
          color $color_theme
        &:nth-child(2)
          font-size 0.8rem
          color $color_black_text_0
</style>



